	module.exports = {"data":{"post":{"html":"<p>I don't know how we managed it, but we wore Christopher completely out yesterday. He fell asleep on the living room floor before we could take him to bed. Too much sandbox, swing set, and Bobby Car!</p>\n<p>We try to avoid 'potty talk' here at PapaScott, but now for the second day in a row, Christopher has come to me when his diaper was full, more or less asking to be changed. Maybe he's implying that I sometimes forget to check.</p>","fields":{"title":"Tired boy","date":"14 May 2001","url":"https://papascott.de/2001-05-14-tired-boy/"}}},"pathContext":{"slug":"/2001-05-14-tired-boy/","prev":{"fields":{"title":"42","date":"13 May 2001","slug":"/2001-05-13-42/"}},"next":{"fields":{"title":"Road Trip","date":"16 May 2001","slug":"/2001-05-16-road-trip/"}}}}

